import React from "react"
import ReactModal from "react-modal"
import { Link } from "gatsby"

const Layout = ({ location, title, children }) => {
  const [showModal, setShowModal] = React.useState(false)
  const rootPath = `${__PATH_PREFIX__}/`
  const isRootPath = location.pathname === rootPath
  let header

  if (isRootPath) {
    header = (
      <>
        <div className="main-heading">
          <h6 className="logo-h6">
            <Link to="/">{title}</Link>
          </h6>
        </div>
        {/* <div className="about-heading">
          <h6 className="about-h6">
            <Link to="/about">About</Link>
          </h6>
        </div> */}
      </>
    )
  } else {
    header = (
      <div className="main-heading">
        <h6 className="logo-h6">
          <Link to="/">{title}</Link>
        </h6>
      </div>
    )
  }

  return (
    <div data-is-root-path={isRootPath}>
      <header className="global-header">{header}</header>
      <div className="global-wrapper">
        <main>{children}</main>
      </div>
      <footer className="global-footer">
        <div className="footer-wrapper">
          <div className="footer-content">
            <strong>Neverblur</strong>
            <p>&copy; {new Date().getFullYear()} Neverblur</p>
            <strong>Contact Us</strong>
            <a href="mailto:learnillion@gmail.com">learnillion@gmail.com</a>
          </div>
          <div className="footer-content">
            <button
              className="subscribe-button"
              onClick={() => setShowModal(true)}
            >
              <p>Stay up to date with Neverblur Blog</p>
            </button>
            <div className="social-wrapper">
              <p>Connect with us: </p>
              <a
                href="https://twitter.com/neverblurhq"
                rel="noopener noreferrer"
                aria-label="Twitter"
              >
                <img
                  loading="lazy"
                  src="data:image/svg+xml;base64,"
                />
              </a>
            </div>
          </div>
        </div>
      </footer>
      <ReactModal
        isOpen={showModal}
        onRequestClose={() => setShowModal(false)}
        className="subscribe-modal"
        overlayClassName="subscribe-modal-overlay"
        shouldCloseOnOverlayClick={true}
        shouldCloseOnEsc={true}
      >
        <div className="subscribe-modal-content">
          <button
            className="subscribe-modal-close-button"
            onClick={() => setShowModal(false)}
          >
            <p>✕</p>
          </button>
          <iframe
            src="https://cdn.forms-content.sg-form.com/4197bc66-6248-11eb-95a5-ee005c614440"
            className="sendgrid-iframe"
          />
        </div>
      </ReactModal>
    </div>
  )
}

export default Layout
